* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: rgb(236, 236, 236);
}

.login {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: "Inter", sans-serif;
}

.login__form {
    display: flex;
    flex-direction: column;
}

.login__form>h1 {
    font-size: 35px;
    margin-bottom: 15px;
    padding-bottom: 10px;
    letter-spacing: -1px;
}

.login__form>input {
    min-width: 380px;
    padding: 20px 0;
    padding-left: 15px;
    margin-bottom: 10px;
    outline: none;
    border: 1px solid rgba(0, 0, 0, 0.24);
    font-family: "Inter", sans-serif;
    border-radius: 2px;
    font-size: 15px;
}

.submit__btn {
    padding: 17px 30px;
    margin-top: 10px;
    background: #000;
    border: none;
    color: white;
    font-size: 14px;
    font-family: "Inter", sans-serif;
    border-radius: 3px;
}

.logout {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: "Inter", sans-serif;
}

.logout__button {
    min-width: 300px;
    padding: 17px 30px;
    margin-top: 20px;
    background: black;
    border: none;
    color: white;
    font-size: 14px;
    font-family: "Inter", sans-serif;
    border-radius: 3px;
}

.user__name {
    color: #8d0ada;
}